/*eslint-disable*/
import React from "react";
import { Container } from "reactstrap";
import Countdown from "react-countdown";
import moment from "moment";

function IndexHeader() {
  let pageHeader = React.createRef();

  const getMaintDate = () => {
    let maintMom = moment.utc().day(4);
    maintMom.set("hour", 7);
    maintMom.set("minute", 0);
    maintMom.set("second", 0);
    maintMom.set("millisecond", 0);
    return maintMom.toDate();
  };

  React.useEffect(() => {
    if (window.innerWidth > 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <>
      <div className="page-header clear-filter" filter-color="blue">
        <div
          className="page-header-image"
          style={{
            backgroundImage: "url(" + require("assets/img/bg.jpg") + ")",
            filter: "brightness(0.7)",
          }}
          ref={pageHeader}
        ></div>
        <Container>
          <div className="content-center brand">
            <img
              alt="..."
              className="n-logo"
              src={require("assets/img/site_icon.png")}
            ></img>
            <h1 className="h1-seo">Azur Lane's EN Community Tier List</h1>
            <h3>
              Created and supported by #gameplay-help and a bunch of AL veterans
              from various servers
            </h3>
          </div>
          <div className="category category-absolute">
            <h3>Countdown to next Maintenance:</h3>
            <h1>
              <Countdown date={getMaintDate()} />
            </h1>
          </div>
        </Container>
      </div>
    </>
  );
}

export default IndexHeader;
