import React from "react";

import { Card, CardBody, Container, Row, Col } from "reactstrap";

import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";

function Faqs() {
  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <div
        className="sub-page-background"
        style={{
          backgroundImage: "url(" + require("assets/img/bg.jpg") + ")",
        }}
      >
        <IndexNavbar isStatic={true} />
        <Container style={{ color: "#000" }}>
          <Col className="ml-auto mr-auto" md="8">
            <Card>
              <CardBody>
                <h3>Frequently Asked Questions</h3>
                <Row>
                  <Col>
                    <ul className="text-left">
                      <li>
                        <p>Q: Why is the tier list sorted by hull types?</p>
                        <p>
                          A: The tier list is sorted by hull types for the sake
                          of readability, maintainability, and to keep the
                          spirit of a tier list being a power ranking of sorts.
                          We assume users either understand, or will get their
                          information elsewhere, about the use cases and
                          dynamics of the different hull types.
                        </p>
                      </li>
                      <li>
                        <p>Q: How are the ships tiered?</p>
                        <p>
                          A: We tier ships based on them being used to their 
                          potential in terms of gear, levels, skills (FS5), 
                          fleet composition (but not specific ships), and being 
                          used against enemies they are effective against which 
                          can be found during general gameplay. For example, 
                          torpedo-focused ships being tiered assuming a fight 
                          against heavy armor is valid, but tiering ships 
                          because they excel against a specific enemy or battle 
                          is not. We also tier without consideration of 
                          incoming buffs from other ships, with the exceptions 
                          of when said ships specifically mentions that they 
                          buff the tiered ship in question by name, or when the 
                          tiered ship in question activates a buff when a fleet 
                          condition is met. These are indicated with the 
                          "Paired" tag and are valid as long as the requirement 
                          does not lead to a suboptimal fleet. AA is typically
                          not weighted heavily, but since it is very relevant 
                          in the currently hardest content in the game, it has 
                          a heavier weight for ships with an exceptional amount 
                          of AA, as using said ships will give tremendous 
                          flexibility for picking the rest of the ships in the 
                          fleet. Tiering is also assuming "auto" gameplay, but 
                          some leniency is given for torpedo-focused vanguard 
                          ships. Lastly, cost is not a factor when tiering.
                        </p>
                        <p>
                          Tiering starts at T0, where we place the ships 
                          that excel and stand out at performing many roles, 
                          or a specific role, typical of their hull type. We 
                          then move down in half-tier increments, placing 
                          ships that perform the same role or roles slightly 
                          worse, lost utility, or have some notable weakness, 
                          compared to the ships in the tier above. Ships 
                          with unique skills, utility, or gimmicks are tiered a 
                          bit more subjectively, placed based on a consensus 
                          between the evaluators of how likely we are to use 
                          the ship compared to others ships in the various 
                          tiers of the hull type. T2 is the cut-off for tiering  
                          and anything underneath that are untiered as TN.
                        </p>
                      </li>
                      <li>
                        <p>Q: How are ships ordered within a tier?</p>
                        <p>
                          A: They are ordered first by rarity, and then 
                          alphabetically. We do not do rankings within the same
                          tier.
                        </p>
                      </li>
                    </ul>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Container>
      </div>
      <DarkFooter />
    </>
  );
}

export default Faqs;
